#masthead.site-header.sticky .container .custom-logo-link img {max-height: 60px;max-width: 60px;}
#masthead.site-header {position: absolute;display: contents;display: block;top: 0;left: 0;z-index: 1;padding-top: 20px;transition: .7s all ease;}
