/* Scroll to Top */
.scroll-to-top.right-scroll {
	display: inline-block;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	font-weight: 400;
	font-style: normal;
	font-variant: normal;
	-webkit-font-smoothing: antialiased;
	line-height: 1;
	text-transform: none;
	content: attr(data-icon);
	display: none;
	position: fixed;
	z-index: 99999;
	right: 0;
	bottom: 15vh;
	padding: 5px;
	-webkit-border-top-left-radius: 5px;
	-moz-border-radius-topleft: 5px;
	border-top-left-radius: 5px;
	-webkit-border-bottom-left-radius: 5px;
	-moz-border-radius-bottomleft: 5px;
	border-bottom-left-radius: 5px;
	color: #fff;
	background: rgba(0,0,0,.4);
	font-size: 30px;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
}
.scroll-to-top.right-scroll.sticky {
	display: block;
}
.scroll-to-top.right-scroll i {
	font-size: 30px;
	line-height: 35px;
}

// page-realisations.php
@-webkit-keyframes up_down_animation {50% {transform: translateY(-10px);} 100% {transform: translateY(0);}}
@keyframes up_down_animation {50% {transform: translateY(-10px);} 100% {transform: translateY(0);}}
.aquila-realisations {
	transition: filter 900ms ease 0ms;
	-webkit-animation-name: up_down_animation;
	animation-name: up_down_animation;
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
	-webkit-animation-timing-function: linear;
	-moz-animation-timing-function: linear;
	-o-animation-timing-function: linear;
	animation-timing-function: linear;
}
.aquila-realisations img {
    transition: box-shadow 300ms ease 0ms,box-shadow 300ms ease 0ms,box-shadow 300ms ease 0ms;
    min-width: 250px;
    max-width: 250px;
    min-height: 250px;
    max-height: 250px;
    border-radius: 150px;
    box-shadow: none;
}
.aquila-realisations img:hover {box-shadow: 20px 0 70px #444;}